@import "../inc/config";
* {
  box-sizing: border-box;
  user-select: none;
}

li,
ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;

  &:focus,
  &:hover {
    text-decoration: none;
    color: $color2;
  }
}

html {
  background: $color0;
}

body {
  color: #fff;
  font: 400 14px/14px $font;
  min-height: 100vh;
  width: 100%;
}
